<template>
  <view class="pay-container">
    <view class="status-box">
      <view class="icon-alipay">üî∑</view>
      <view class="status-text">{{ statusText }}</view>
      <view class="amount-text">¬•{{ amount }}</view>
    </view>

    <!-- H5 ÁéØÂ¢É‰∏ãÁöÑË°®ÂçïÂÆπÂô® -->
    <!-- #ifdef H5 -->
    <view v-html="alipayForm" ref="formContainer" style="display: none;"></view>
    <!-- #endif -->

    <view class="action-box">
      <!-- #ifdef MP-WEIXIN -->
      <view class="mini-tips">
        <text>Áî±‰∫éÂπ≥Âè∞ÈôêÂà∂ÔºåËØ∑Â§çÂà∂ÊîØ‰ªòÈìæÊé•Âà∞ÊµèËßàÂô®ÊâìÂºÄ</text>
      </view>
      <button class="btn-copy" @click="copyPayLink">Â§çÂà∂ÊîØ‰ªòÈìæÊé•</button>
      <!-- #endif -->
      
      <!-- #ifndef MP-WEIXIN -->
      <button class="btn-submit" @click="submitPay">Á´ãÂç≥ÊîØ‰ªò</button>
      <!-- #endif -->
      
      <button class="btn-cancel" @click="goBack">ËøîÂõû</button>
    </view>
    
    <view class="query-tips" v-if="orderId">
      <text>ÊîØ‰ªòÂÆåÊàêÂêéÔºåÁ≥ªÁªüÂ∞ÜËá™Âä®Ê£ÄÊµãÁä∂ÊÄÅÂπ∂ÂõûË∑≥</text>
    </view>

    <view class="check-box" v-if="isPaid">
      <text class="success-tip">‚úÖ Ê£ÄÊµãÂà∞ÊîØ‰ªòÂ∑≤ÂÆåÊàê</text>
    </view>
    <button v-else-if="alipayForm" class="btn-check" @click="checkPayStatus">ÊàëÂ∑≤ÂÆåÊàêÊîØ‰ªòÔºåÁÇπÂáªÂà∑Êñ∞</button>
  </view>
</template>

<script>
import { getWalletBalance, syncAlipayStatus } from '@/api/wallet.js'
import { getOrderDetail } from '@/api/order.js'

export default {
  data() {
    return {
      alipayForm: '',
      amount: '0.00',
      orderId: '',
      type: 'recharge', // recharge Êàñ order
      statusText: 'Ê≠£Âú®ÂáÜÂ§áÊîØ‰ªò...',
      isPaid: false,
      timer: null,
      oldBalance: 0
    }
  },
  async onLoad(options) {
    this.amount = options.amount || '0.00'
    this.orderId = options.orderId || ''
    this.type = options.type || 'recharge'
    
    // Â¶ÇÊûúÊòØÂÖÖÂÄºÊ®°ÂºèÔºåÂÖàËé∑ÂèñÂΩìÂâç‰ΩôÈ¢ùÔºåÁî®‰∫éÂêéÁª≠ÂØπÊØîÂà§Êñ≠ÊòØÂê¶Âà∞Ë¥¶
    if (this.type === 'recharge') {
      await this.getInitialBalance()
    }
    
    // ‰ªéÁºìÂ≠òËé∑ÂèñÊîØ‰ªòË°®Âçï
    const form = uni.getStorageSync('alipay_form')
    console.log('üì¶ Ëé∑ÂèñÂà∞ÊîØ‰ªòË°®ÂçïÂÜÖÂÆπ:', form)
    
    if (form) {
      this.alipayForm = typeof form === 'string' ? form.trim() : form
      // Â¶ÇÊûúÊòØ H5 ÁéØÂ¢ÉÔºåÂ∞ùËØïËá™Âä®Ë∑≥ËΩ¨
      // #ifdef H5
      this.statusText = 'Ê≠£Âú®Ë∑≥ËΩ¨Âà∞ÊîØ‰ªòÂÆù...'
      setTimeout(() => {
        this.submitPay()
      }, 500)
      // #endif
      
      // #ifdef MP-WEIXIN
      this.statusText = 'ËØ∑Â§çÂà∂ÈìæÊé•ÊîØ‰ªò'
      // ÂêØÂä®ËΩÆËØ¢Ê£ÄÊü•
      this.startPolling()
      // #endif
    } else {
      uni.showToast({
        title: 'ÊîØ‰ªò‰ø°ÊÅØ‰∏¢Â§±',
        icon: 'none'
      })
      setTimeout(() => {
        uni.navigateBack()
      }, 1500)
    }
  },
  onUnload() {
    this.stopPolling()
  },
  methods: {
    async getInitialBalance() {
      try {
        const res = await getWalletBalance()
        if (res.code === 200) {
          this.oldBalance = Number(res.data || 0)
        }
      } catch (e) {}
    },

    startPolling() {
      this.stopPolling()
      this.timer = setInterval(() => {
        this.checkPayStatus(true)
      }, 3000)
    },

    stopPolling() {
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },

    /**
     * Ê£ÄÊü•ÊîØ‰ªòÁä∂ÊÄÅ
     * @param {Boolean} isAuto ÊòØÂê¶‰∏∫Ëá™Âä®ËΩÆËØ¢
     */
    async checkPayStatus(isAuto = false) {
      if (this.isPaid) return

      // 1. Ëé∑ÂèñÂïÜÊà∑ËÆ¢ÂçïÂè∑Âπ∂Â∞ùËØïËß¶ÂèëÂêéÁ´ØË°•ÂçïÂêåÊ≠•
      const outTradeNo = this.getOutTradeNo()
      if (outTradeNo) {
        console.log('üîÑ Ëß¶ÂèëÂêéÁ´ØÁä∂ÊÄÅÂêåÊ≠•:', outTradeNo)
        try {
          await syncAlipayStatus(outTradeNo)
        } catch (e) {
          console.error('ÂêåÊ≠•Áä∂ÊÄÅÊé•Âè£ÂºÇÂ∏∏:', e)
        }
      }

      // 2. Ê£ÄÊü•Êú¨Âú∞‰∏öÂä°Áä∂ÊÄÅ
      try {
        if (this.type === 'recharge') {
          // ÂÖÖÂÄºÊ®°ÂºèÔºöÊ£ÄÊü•‰ΩôÈ¢ùÊòØÂê¶ÂèòÂä®
          const res = await getWalletBalance()
          if (res.code === 200) {
            const currentBalance = Number(res.data || 0)
            if (currentBalance > this.oldBalance) {
              this.handleSuccess()
            } else if (!isAuto) {
              uni.showToast({ title: 'Â∞öÊú™Ê£ÄÊµãÂà∞ÂÖÖÂÄºÂà∞Ë¥¶', icon: 'none' })
            }
          }
        } else {
          // ËÆ¢ÂçïÊ®°ÂºèÔºöÊ£ÄÊü•ËÆ¢ÂçïÁä∂ÊÄÅ
          const res = await getOrderDetail(this.orderId)
          if (res.code === 200 && res.data) {
            // Áä∂ÊÄÅ 20-ÂæÖÊé•Âçï Êàñ 30-ÈÖçÈÄÅ‰∏≠ ÂùáË°®Á§∫ÊîØ‰ªòÊàêÂäü
            const status = res.data.status
            if (status >= 20 && status < 60) {
              this.handleSuccess()
            } else if (!isAuto) {
              uni.showToast({ title: 'ËÆ¢ÂçïÂ∞öÊú™ÊîØ‰ªòÊàêÂäü', icon: 'none' })
            }
          }
        }
      } catch (e) {
        console.error('Ê£ÄÊü•ÊîØ‰ªòÁä∂ÊÄÅÂºÇÂ∏∏:', e)
      }
    },

    handleSuccess() {
      this.isPaid = true
      this.stopPolling()
      uni.showToast({ title: 'ÊîØ‰ªòÊàêÂäü', icon: 'success' })
      
      setTimeout(() => {
        if (this.type === 'recharge') {
          uni.switchTab({ url: '/pages/mine/mine' })
        } else {
          uni.redirectTo({ url: `/pages/order/detail?id=${this.orderId}` })
        }
      }, 1500)
    },
    /**
     * Êèê‰∫§ÊîØ‰ªòÔºàH5ÁéØÂ¢ÉÔºâ
     */
    submitPay() {
      // #ifdef H5
      if (this.alipayForm.startsWith('http')) {
        // Â¶ÇÊûúÊòØ URL Áõ¥Êé•Ë∑≥ËΩ¨
        window.location.href = this.alipayForm
      } else {
        // Â¶ÇÊûúÊòØ Form Ëá™Âä®Êèê‰∫§
        document.body.insertAdjacentHTML('beforeend', this.alipayForm)
        document.forms[0].submit()
      }
      // #endif

      // #ifndef H5
      this.copyPayLink()
      // #endif
    },

    /**
     * Â§çÂà∂ÊîØ‰ªòÈìæÊé•ÔºàÂ∞èÁ®ãÂ∫èÁéØÂ¢ÉÔºâ
     */
    copyPayLink() {
      console.log('üîó ÂáÜÂ§áËß£ÊûêÊîØ‰ªòÈìæÊé•, ÂÜÖÂÆπÈïøÂ∫¶:', this.alipayForm.length)
      let payUrl = ''
      
      const trimmedForm = this.alipayForm.trim()
      
      if (trimmedForm.startsWith('http')) {
        payUrl = trimmedForm
      } else {
        // Â∞ùËØï‰ªé Form ‰∏≠ÊèêÂèñ action (ÂÖºÂÆπÂçïÂºïÂè∑ÂíåÂèåÂºïÂè∑)
        const actionMatch = trimmedForm.match(/action=["']([^"']+)["']/)
        if (actionMatch && actionMatch[1]) {
          payUrl = actionMatch[1]
          console.log('‚úÖ ‰ªéË°®ÂçïËß£ÊûêÂá∫ URL:', payUrl)
        }
      }

      if (payUrl) {
        uni.setClipboardData({
          data: payUrl,
          success: () => {
            console.log('‚úÖ ÈìæÊé•Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
            uni.showModal({
              title: 'ÈìæÊé•Â∑≤Â§çÂà∂',
              content: 'ÊîØ‰ªòÈìæÊé•Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÔºåËØ∑ÊâìÂºÄÊµèËßàÂô®Á≤òË¥¥Âπ∂ÊîØ‰ªò„ÄÇ',
              showCancel: false,
              confirmText: 'ÊàëÁü•ÈÅì‰∫Ü'
            })
          }
        })
      } else {
        console.error('‚ùå Ëß£ÊûêÊîØ‰ªòÈìæÊé•Â§±Ë¥•, ÂéüÂÜÖÂÆπ:', this.alipayForm)
        uni.showToast({
          title: 'Ëß£ÊûêÊîØ‰ªòÈìæÊé•Â§±Ë¥•',
          icon: 'none'
        })
      }
    },

    goBack() {
      uni.navigateBack()
    },

    /**
     * ‰ªéË°®ÂçïÊàñ URL ‰∏≠ÊèêÂèñÂïÜÊà∑ËÆ¢ÂçïÂè∑ (out_trade_no)
     */
    getOutTradeNo() {
      if (!this.alipayForm) return ''
      const trimmedForm = this.alipayForm.trim()
      
      // 1. Â∞ùËØï‰ªé URL ÂèÇÊï∞‰∏≠ÊèêÂèñ (GET ÊñπÂºè)
      const urlMatch = trimmedForm.match(/[?&]out_trade_no=([^&]+)/)
      if (urlMatch && urlMatch[1]) return urlMatch[1]
      
      // 2. Â∞ùËØï‰ªé HTML Input ‰∏≠ÊèêÂèñ (Form ÊñπÂºè)
      const inputMatch = trimmedForm.match(/name=["']out_trade_no["']\s+value=["']([^"']+)["']/)
      if (inputMatch && inputMatch[1]) return inputMatch[1]
      
      // 3. ÂÖºÂÆπÊ≤°ÊúâÂºïÂè∑ÁöÑÊÉÖÂÜµÊàñÂÖ∂‰ªñÂ±ûÊÄßÈ°∫Â∫è
      const altMatch = trimmedForm.match(/value=["']([^"']+)["']\s+name=["']out_trade_no["']/)
      if (altMatch && altMatch[1]) return altMatch[1]
      
      return ''
    }
  }
}
</script>

<style lang="scss" scoped>
.pay-container {
  padding: 40rpx;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f8f8f8;
}

.status-box {
  margin-top: -100rpx;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 80rpx;
}

.icon-alipay {
  font-size: 120rpx;
  margin-bottom: 30rpx;
}

.status-text {
  font-size: 34rpx;
  color: #333;
  font-weight: bold;
  margin-bottom: 20rpx;
}

.amount-text {
  font-size: 60rpx;
  color: #f40;
  font-weight: bold;
}

.action-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 30rpx;
}

.mini-tips {
  font-size: 26rpx;
  color: #999;
  text-align: center;
  margin-bottom: 20rpx;
}

.btn-submit, .btn-copy {
  width: 100%;
  height: 90rpx;
  line-height: 90rpx;
  border-radius: 45rpx;
  background: linear-gradient(to right, #00a0e9, #0076c8);
  color: #fff;
  font-size: 32rpx;
}

.btn-cancel {
  width: 100%;
  height: 90rpx;
  line-height: 90rpx;
  border-radius: 45rpx;
  background: #fff;
  color: #666;
  font-size: 30rpx;
  border: 1px solid #ddd;
}

.btn-check {
  width: 100%;
  height: 90rpx;
  line-height: 90rpx;
  border-radius: 45rpx;
  background: #fff;
  color: #00a0e9;
  font-size: 30rpx;
  border: 1px solid #00a0e9;
  margin-top: 20rpx;
}

.check-box {
  margin-top: 40rpx;
  padding: 20rpx;
  background-color: #e6f7ff;
  border-radius: 12rpx;
  border: 1px solid #91d5ff;
}

.success-tip {
  color: #1890ff;
  font-size: 28rpx;
  font-weight: bold;
}

.query-tips {
  margin-top: 60rpx;
  font-size: 24rpx;
  color: #999;
}
</style>
